<template>
  <div id="bgimg">
    <h1>Welcome to the Admin homepage</h1>
    <div class="container">
      <div class="table-container">
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{ backgroundColor: '#f0f0f0' }"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="id" label="ID"></el-table-column>
          <el-table-column prop="loginName" label="UserName"></el-table-column>
          <el-table-column prop="name" label="ScreenName"></el-table-column>
          <el-table-column prop="password" label="Password"></el-table-column>
          <el-table-column label="Operation">
            <template slot-scope="scope">
              <el-button type="danger" @click="deleteRow(scope.row)">
                Delete
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      tableData: [], // 用于存储从数据库中获取的数据
    };
  },
  mounted() {
    // 在组件挂载后立即获取数据
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios
        .get('http://localhost:3231/sys-user/Admin')
        .then((response) => {
          this.tableData = response.data; // 将获取的数据存储到组件的tableData属性中
        })
        .catch((error) => {
          console.error('Error fetching data:', error);
        });
    },
    tableRowClassName({ rowIndex }) {
      return rowIndex % 2 === 0 ? 'row-even' : 'row-odd';
    },
    deleteRow(row) {
      // 向后端发送删除请求
      axios
        .delete(`http://localhost:3231/sys-user/${row.id}`)
        .then(() => {
          const index = this.tableData.indexOf(row);
          if (index !== -1) {
            this.tableData.splice(index, 1);
            this.$message.success('Successfully deleted');
          }
        })
        .catch((error) => {
          console.error('Error deleting row:', error);
          this.$message.error('Deletion failure');
        });
    },
  },
};
</script>

<style scoped>
#bgimg {
    background: url('~@/assets/bgimg.png') no-repeat center center;
    width: 100%;
    min-height: 100vh;
    position: fixed;
    background-size: cover;
    }

.container {
  position: relative;
  height: 100vh;
}

.table-container {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  overflow: auto;
  max-height: 80vh;
}

.row-even {
  background-color: #f9f9f9;
}

.row-odd {
  background-color: #5b5757;
}

::v-deep .el-table th,
::v-deep .el-table td {
  padding: 10px;
  font-size: 15px; /* Increase the font size */
}
</style>
