<template>
  <div id="bgimg">
  <div id="poster">
    <el-form 
    :model="ruleForm" 
    :rules="rules"
    ref="ruleForm" 
    label-width="0px"
    label-position="left"
     class="register-container">
        <h3 class="register_title">
          Register
        </h3>
    <el-form-item label="" prop="loginName">
        <el-input 
        type="text"
        autocomplete="off"
        v-model="ruleForm.loginName"
        placeholder="Please enter your username"
        prefix-icon="el-icon-user-solid"
        ></el-input>
    </el-form-item>
        <el-form-item label="" prop="name">
        <el-input 
         type="text"
         autocomplete="off"
        v-model="ruleForm.name"
        prefix-icon="el-icon-user-solid"        
        placeholder="Please enter a screen name"></el-input>
    </el-form-item>
    
    <el-form-item label="" prop="password">
        <el-input 
        type="password" 
        v-model="ruleForm.password"
         autocomplete="off"
         placeholder="Please enter password"
          prefix-icon="el-icon-lock"  

         ></el-input>
    </el-form-item>
    <el-form-item label="" prop="checkPass">
        <el-input 
        type="password" 
        v-model="ruleForm.checkPass" 
        placeholder="Please enter your password again"
          prefix-icon="el-icon-lock"  
        autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item>
        <el-button type="primary" style="background:#505458;border:none" @click="submitForm(ruleForm)">Register</el-button>
        <el-button @click="resetForm('ruleForm')">Reset</el-button>
      </el-form-item>
      <el-button @click="toLogin()">Click to login</el-button>
    </el-form>
  </div>
</div>
</template>

<script>

export default {
  name: 'Register',
  data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please enter password'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please enter your password again'));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('Two password inconsistencies!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          loginName: '',
          password: '',
          name: '',
          checkPass:""
        },
        rules: {
          loginName: [
            { required: true,message:"Please enter a screen name", trigger: 'blur' }
          ],
          password: [
            { validator: validatePass, trigger: 'blur' },
            { min: 2,max:9,message:"Length: 2 to 9 characters"}
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' },
            { min: 2,max:9,message:"Length: 2 to 9 characters"}
          ]
        }
      };
    },
    methods: {
  submitForm(ruleForm) {
    this.axios.post('http://localhost:3231/sys-user/register', this.ruleForm).then((resp) => {
      console.log(resp);
      let data = resp.data;
      if (data.success) {
        this.ruleForm = {};
        this.$message({
          message: 'Congratulations, registration success!!!',
          type: 'success'
        });
      } else {
        if (data.message === 'The user name already exists') {
          this.$message({
            message: 'The user name has been registered, please re-register',
            type: 'error'
          });
        }  
      }
    }).catch((error) => {
      console.error(error);
      this.$message({
        message: 'Formatting error',
        type: 'error'
      });
    });
  },
  resetForm(formName) {
    this.$refs[formName].resetFields();
  },
  toLogin() {
    this.$router.push({ path: '/' });
  }
}


}
</script>

<style>
    #poster{
        background-position:center;
        height:100%;
        width:100%;
        background-size:cover;
        position:fixed;
        margin:0px;
        padding:0px;        
    }
    #bgimg {
    background: url('~@/assets/bgimg.png') no-repeat center center;
    width: 100%;
    min-height: 100vh;
    position: fixed;
    background-size: cover;
    }
    .register-container{
        border-radius:15px;
        background-clip:padding-box;
        margin:90px auto;
        width:350px;
        padding: 35px 35px 15px 35px;
        background:#fff;
        border:1px solid #eaeaea;
        box-shadow:0 0 25px #cac6c6;
    }
    .register_title{
        margin : 0px auto 40px auto;
        text-align:center;
        color:#505458;
    }
</style>